@import "../style/sanitize.css";
@import "../style/typo.css";

@import "./header.css";

* {
   box-sizing: border-box;
}

body {
  text-rendering: optimizeLegibility;
}

.layout,
.header,
.main,
.footer {
  display: flex;
  flex-direction: column;
}

.layout {
  min-height: 100vh;
}

.main {
  flex-grow: 1;
}

.container {
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
}

.header {
  background: var(--grayDark);
}




